export function menuMapLeafKeys(menus: any[]) {
  const leftKeys: number[] = []
  const _recurseGetLeaf = (menus: any[]) => {
    for (const menu of menus) {
      if (menu.children) {
        _recurseGetLeaf(menu.children)
      } else {
        leftKeys.push(menu.id)
      }
    }
  }
  _recurseGetLeaf(menus)
  return leftKeys
}

export { fristMenu }
