import { Module } from 'vuex'
import { IloginState } from './type'
import { IRootState } from '../type'
import {
  accountLoginRequest,
  requestUserInfoById,
  requestUserMenuById
} from '@/service/login/login'
import { IAccount } from '@/service/login/type'
import localCache from '@/utils/cache'
import { mapMenusToPermissions, mapMenusToRoutes } from '@/utils/map-menus'
import router from '@/router'

const loginModule: Module<IloginState, IRootState> = {
  namespaced: true,
  state() {
    return {
      token: '',
      userInfo: '',
      userMenus: '',
      permissions: []
    }
  },
  getters: {},
  mutations: {
    changeToken(state, token: string) {
      state.token = token
    },
    changeUserInfo(state, userInfo) {
      state.userInfo = userInfo
    },
    changeUserMenu(state, userMenu) {
      state.userMenus = userMenu

      const routers = mapMenusToRoutes(userMenu)
      // console.log('注册动态路由')

      routers.forEach((route) => {
        // console.log('添加子路由')
        router.addRoute('main', route)
      })
      // console.log(router)
      const permissions = mapMenusToPermissions(userMenu)
      state.permissions = permissions
    }
  },
  actions: {
    async accountLoginAction({ commit, dispatch }, payload: IAccount) {
      console.log('执行accountLoginAction')
      console.log('拿到payload数据', payload)
      const loginResult = await accountLoginRequest(payload)
      console.log('根据账号密码请求来的数据', loginResult)
      const { id, token } = loginResult.data
      localCache.setCache('token', token)
      dispatch('getInitialDataAction', null, { root: true })

      commit('changeToken', token)

      const userInfoResult = await requestUserInfoById(id)
      const userInfo = userInfoResult.data
      console.log('根据id拿到的userInfo数据', userInfo)
      localCache.setCache('userInfo', userInfo)
      commit('changeUserInfo', userInfo)

      const userMenuResult = await requestUserMenuById(userInfo.role.id)
      const userMenu = userMenuResult.data
      console.log('根据id拿到的userMenu数据', userMenu)
      commit('changeUserMenu', userMenu)
      localCache.setCache('userMenus', userMenu)

      // 4.跳到首页
      router.push('/main')
    },
    loadLocalLogin({ commit, dispatch }) {
      const token = localCache.getCache('token')
      if (token) {
        commit('changeToken', token)
        dispatch('getInitialDataAction', null, { root: true })
      }
      const userInfo = localCache.getCache('userInfo')
      if (userInfo) {
        commit('changeUserInfo', userInfo)
      }
      const userMenu = localCache.getCache('userMenus')
      if (userMenu) {
        commit('changeUserMenu', userMenu)
      }
    }

    // phoneLoginAction({ commit }, payload) {
    //   console.log('执行phoneLoginAction')
    //   console.log('拿到payload数据', payload)
    // }
  }
}

export default loginModule
